<template>
  <div class="invoice-normal">
    <ul>
      <!-- <li class="item">
        <div class="left">发票信息</div>
      </li> -->
      <li class="item radio">
        <div class="left">抬头类型</div>
        <div class="right">
        <van-radio-group v-model="radio" :disabled="isLocked===1" @change="fnRadioChange">
          <!-- <van-radio checked-color="#2283E2" icon-size="16px" name="1">个人</van-radio>
          <van-radio checked-color="#2283E2" icon-size="16px" name="2">公司</van-radio> -->
          <van-radio checked-color="#2283E2" name="1">
            个人
            <template #icon="props">
              <img class="imgIcon" :src="props.checked ? activeIcon : inactiveIcon" />
            </template>
          </van-radio>
          <van-radio checked-color="#2283E2" name="2">
            公司
            <template #icon="props">
              <img class="imgIcon" :src="props.checked ? activeIcon : inactiveIcon" />
            </template>
          </van-radio>
        </van-radio-group>
        </div>
      </li>
      <li class="item">
        <div class="left">发票抬头</div>
        <div class="right">
          <!-- <input placeholder="可输入个人／单位名称" type="text" v-model="iti" :disabled="isLocked===1" @input="fnInput('iti')" /> -->
          <van-field
            class="field-textarea"
            v-model="iti"
            rows="1"
            :disabled="isLocked===1"
            @input="fnInput('iti')"
            autosize
            type="textarea"
            placeholder="可输入个人／单位名称"
          />
        </div>
      </li>
      <li class="item" v-if="radio === '2'">
        <div class="left">纳税人识别号</div>
        <div class="right">
          <input placeholder="长度为15位或18位或20位" type="text" v-model="tpn" :disabled="isLocked===1" @input="fnInput('tpn')" />
        </div>
      </li>
      <li class="item" v-if="autoFlag && invoiceData.it === 2">
        <div class="left">收票邮箱</div>
        <div class="right">
          <!-- <input placeholder="发票开具后会发送到您的邮箱" type="text" v-model="em" :disabled="isLocked===1" @input="fnInput('em')" /> -->
          <van-field
            class="field-textarea"
            v-model="em"
            rows="1"
            :disabled="isLocked===1"
            @input="fnInput('em')"
            autosize
            type="textarea"
            placeholder="发票开具后会发送到您的邮箱"
          />
        </div>
      </li>
      </ul>
      <ul v-if="hasWa === 0 && invoiceData.it === 3">
       <li class="item">
        <div class="left">收件人姓名</div>
        <div class="right">
          <!-- <input placeholder="请填写收件人姓名" type="text" v-model="rcc" :disabled="isLocked===1" @input="fnInput('rcc')" /> -->
          <van-field
            class="field-textarea"
            v-model="rcc"
            rows="1"
            :disabled="isLocked===1"
            @input="fnInput('rcc')"
            autosize
            type="textarea"
            placeholder="请填写收件人姓名"
          />
        </div>
      </li>

       <li class="item address">
        <div class="left">所在地区</div>
        <div class="right" @click="showLocation">
           <div class="img">
             <span class="location-text">{{this.invoiceData.rcr?this.invoiceData.rcr:'选择'}}</span>
            <img
              class="right-icon"
              :src="`${$cdnImageUrl}/common/right-arrow-gray.png`"
              alt=""
            />
          </div>
        </div>
      </li>

       <li class="item">
        <div class="left">详细地址</div>
        <div class="right">
          <!-- <input placeholder="请填写详细地址" type="text" v-model="rca" :disabled="isLocked===1" @input="fnInput('rca')" /> -->
         <!-- <textarea placeholder="请输入详细地址" v-model="rca" :disabled="isLocked===1" @input="fnInput('rca')"></textarea> -->
        <van-field
          class="field-textarea"
          v-model="rca"
          rows="1"
          :disabled="isLocked===1"
          @input="fnInput('rca')"
          autosize
          type="textarea"
          placeholder="请输入详细地址"
        />
        </div>
      </li>

       <li class="item">
        <div class="left">收件人手机</div>
        <div class="right">
          <input placeholder="请填写收件人手机号" type="text" v-model="rcm" :disabled="isLocked===1" @input="fnInput('rcm')" />
        </div>
      </li>
    </ul>
    <div class="edit-location-tip">
      <span v-if="isLocked===1">该订单存在已经申请的开票信息，发票信息不需更改，如需帮助请联系客服</span>
      <span v-else-if="invoiceData.it !== 2">如发票与商品发往不同地址，请修改邮寄地址信息</span>
    </div>
    <div class="btn-wrap">
      <div class="btn"  @click="fnConfirm">确定</div>
      <template v-if="!hideCancelBtn">
        <div class="no-invoice" @click="cancleInvoice" v-if="hasWa === 0 && !orderNo">不开发票</div>
        <div class="no-invoice" @click="doBack" v-if="hasWa === 0 && orderNo">不开发票</div>
      </template>
    </div>
    <van-popup v-model="showDialog" position="bottom" @close="onHide" @click="onHide">
       <change-geographic-location ref='geoLocation' @onHide="onHide" @handleLocation="handleLocation" />
    </van-popup>
  </div>
</template>
<script>
import changeGeographicLocation from '@/components/change-geographic-location/change-geographic-location'

export default {
  props: ['invoiceData', 'isLocked', 'hasWa', 'orderNo', 'autoFlag', 'hideCancelBtn'],
  components: {
    changeGeographicLocation,
  },
  data() {
    return {
      radio: '1',
      iti: '',
      tpn: '',
      rcc: '',
      rca: '',
      rcm: '',
      em: '',
      showDialog: false,
      activeIcon: `${this.$cdnImageUrl}/common/checked.svg`,
      inactiveIcon: `${this.$cdnImageUrl}/cart/icon-unselected.png`
    }
  },
  watch: {
    invoiceData: {
      handler() {
        this.radio = this.invoiceData.nit.toString()
        this.iti = this.invoiceData.iti || ''
        this.tpn = this.invoiceData.tpn || ''

        this.rcc = this.invoiceData.rcc || ''
        this.rca = this.invoiceData.rca || ''
        this.rcm = this.invoiceData.rcm || ''
        this.em = this.invoiceData.em || ''
      },
      immediate: true,
    },
  },
  methods: {
    /**
     * @description 显示地理位置弹窗
     */
    showLocation() {
      // 发票锁定状态不可选择地址
      if (this.isLocked === 1) {
        return
      }
      this.showDialog = true
    },
    fnRadioChange(nit) {
      this.$emit('fnRadioChange', nit * 1)
    },
    /**
     * @description 不开发票
     */
    cancleInvoice() {
      this.$emit('cancleInvoice')
    },
    /**
     * @description 点击选择所在地区 取消关闭回调
     * @param
     */
    onHide() {
      this.showDialog = false
      this.$refs.geoLocation.load()
    },
    /**
     * @description 点击选择所在地区 确认关闭回调
     * @param {pi/provinceId} 省id
     * @param {ci/cityId} 城市id
     * @param {ri/districtId} 区id
     * @param {si/townshipId} 街道id
     * @param {rn/region} 省市区地址
     */
    handleLocation(data) {
      this.showDialog = false
      if (!data) return
      this.pi = data.provinceId
      this.ci = data.cityId
      this.ri = data.districtId
      this.si = data.townshipId
      this.$emit('handleLocation', {
        address: `${data.province} ${data.city} ${data.district}`,
        street: data.township,
      })
    },
    fnInput(type) {
      let _value
      switch (type) {
        case 'iti':
          _value = this.iti
          break
        case 'tpn':
          _value = this.tpn
          break
        case 'rcc':
          _value = this.rcc
          break
        case 'rca':
          _value = this.rca
          break
        case 'rcm':
          _value = this.rcm
          break
        case 'em':
          _value = this.em
          break
      }
      this.$emit('fnInput', {
        type: type,
        value: _value ? (_value.trim ? _value.trim() : _value) : '',
      })
    },
    fnConfirm() {
      this.$emit('fnConfirm')
    },
    doBack() {
      this.$store.dispatch('doHistoryBack')
    }
  },
}
</script>
