        <div class="no-invoice" @click="doBack" v-if="hasWa === 0 && orderNo">不开发票</div>
      </template>
    </div>
    <van-popup v-model="showDialog" position="bottom" @close="onHide" @click="onHide">
       <change-geographic-location ref='geoLocation' @onHide="onHide" @handleLocation="handleLocation" />
    </van-popup>
