<script>
import changeGeographicLocation from '@/components/change-geographic-location/change-geographic-location'
export default {
  props: ['invoiceData', 'isLocked', 'hasWa', 'orderNo', 'hideCancelBtn'],
  components: {
    changeGeographicLocation,
  },
  data() {
    return {
      showDialog: false,
      bc: '',
      tpn: '',
      rga: '',
      rgp: '',
      bn: '',
      cbn: '',
      rcc: '',
      rcr: '',
      rca: '',
      // rcz: '',
      rcm: '',
      pi: '',
      ci: '',
      ri: '',
      si: '',
    }
  },
  watch: {
    invoiceData: {
      handler() {
        this.bc = this.invoiceData.bc || ''
        this.tpn = this.invoiceData.tpn || ''
        this.rga = this.invoiceData.rga || ''
        this.rgp = this.invoiceData.rgp || ''
        this.bn = this.invoiceData.bn || ''
        this.cbn = this.invoiceData.cbn || ''
        this.rcc = this.invoiceData.rcc || ''
        this.rcr = this.invoiceData.rcr || ''
        this.rca = this.invoiceData.rca || ''
        // this.rcz = this.invoiceData.rcz || ''
        this.rcm = this.invoiceData.rcm || ''
      },
      immediate: true,
    },
  },
  methods: {
    fnInput(type) {
      let _value
      switch (type) {
        case 'bc':
          _value = this.bc
          break
        case 'tpn':
          _value = this.tpn
          break
        case 'rga':
          _value = this.rga
          break
        case 'rgp':
          _value = this.rgp
          break
        case 'bn':
          _value = this.bn
          break
        case 'cbn':
          _value = this.cbn
          break
        case 'rcc':
          _value = this.rcc
          break
        case 'rca':
          _value = this.rca
          break
        // case 'rcz':
        //   _value = this.rcz
        //   break
        case 'rcm':
          _value = this.rcm
          break
      }
      this.$emit('fnInput', {
        type: type,
        value: _value ? (_value.trim ? _value.trim() : _value) : '',
      })
    },
    /**
     * @description 不开发票
     */
    cancleInvoice() {
      this.$emit('cancleInvoice')
    },
    fnChangeAddress() {
      // 发票锁定状态不可选择地址
      if (this.isLocked === 1) {
        return
      }
      this.showDialog = !this.showDialog
    },
    /**
     * @description 点击选择所在地区 取消关闭回调
     * @param
     */
    onHide() {
      this.showDialog = false
      this.$refs.geoLocation.load()
    },

    /**
     * @description 点击选择所在地区 确认关闭回调
     * @param {pi/provinceId} 省id
     * @param {ci/cityId} 城市id
     * @param {ri/districtId} 区id
     * @param {si/townshipId} 街道id
     * @param {rn/region} 省市区地址
     */
    handleLocation(data) {
      this.showDialog = false
      if (!data) return
      this.pi = data.provinceId
      this.ci = data.cityId
      this.ri = data.districtId
      this.si = data.townshipId
      this.$emit('handleLocation', {
        address: `${data.province} ${data.city} ${data.district}`,
        street: data.township,
      })
    },
    fnConfirm() {
      this.$emit('fnConfirm')
    },
    doBack() {
      this.$store.dispatch('doHistoryBack')
    }
  },
}
</script>

