<template>
  <div class="invoice-type-container">
  <van-popup v-model="typeShow" position="bottom" :style="{'max-height':'8.26rem'}"  @close="onHide" >
    <div @click.stop class="content">
      <div class="type-header">
        <span class="type-title">发票类型选择</span>
        <span class="type-close" @click="onHide"></span>
      </div>
      <div class="scroll-view">
        <van-radio-group v-model="typeValue" >
            <van-radio v-for="item in invoiceData.supportedInvoiceTypes" :name="item" :key='item' label-position='left' @click='onHide'>
                <div class="type-item">
                    <div class="lable"> {{invoiceType[item] || invoiceType[2]}}</div>
                    <div class="description">{{item===1 || item===3?'纸质发票将在订单关闭7个工作日内开具并安排邮寄':'电子发票与纸质发票具有保修与财务报销效力'}}</div>
                </div>
                <template #icon="props">
                <img class="img-icon" :src="props.checked ? activeIcon : inactiveIcon" />
                </template>
            </van-radio>
        </van-radio-group>
      </div>
    </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  props: ['invoiceData', 'show'],
  data() {
    return {
      typeValue: this.invoiceData.it ? this.invoiceData.it : 2, // 发票类型选中值
      activeIcon: `${this.$cdnImageUrl}/common/radio-selected.png`,
      inactiveIcon: `${this.$cdnImageUrl}/common/radio-unselect.png`,
      typeShow: false,
      closeFlag: false, // 防止hide回调重复
      invoiceType: {
        1: '增值税专用发票',
        3: '增值税纸质普通发票',
        2: '增值税电子普通发票',
        4: '增值税电子专用发票'
      }
    }
  },
  watch: {
    invoiceData: {
      handler(newValue, oldValue) {
        this.typeValue = newValue.it || newValue.invoiceType ? newValue.it || newValue.invoiceType : 2
      },
      immediate: true,
      deep: true
    },
    show: {
      handler(newValue, oldValue) {
        this.typeShow = newValue
        if (newValue) this.closeFlag = false
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
  },
  methods: {
    /**
     * @description 弹窗关闭回调
     */
    onHide() {
      if (!this.closeFlag) {
        this.closeFlag = true
        this.$emit('onHide', this.typeValue)
      }
    }
  }
}
</script>

