<template>
<div>
  <Header>
    <div slot="title">{{ modifyFlag ? '修改发票' : '开具发票' }}</div>
    <div slot="right" v-if="invoiceData.it === 1 || invoiceData.it === 4" @click="$router.push('/manageInvoice')">选择发票信息</div>
  </Header>
  <div class="container">
    <order-info v-if="orderNo && pageInfo && !modifyFlag" :invoiceData="pageInfo"></order-info>
    <div class="type">
      <div class="tit">
        发票信息
      <span class="invoice-tips-container" @click="invoiceTipsShow=true"><span class="tips-icon"></span>发票须知</span>
      </div>
      <!-- <div class="tab">
        <div :class="{ active: invoiceData.it === 2 }" @click="fnChangeType(2)" class="tab-item"><span>普通发票</span></div>
        <div :class="{ active: invoiceData.it === 1 }" @click="fnChangeType(1)" class="tab-item"><span>增值税专用发票</span></div>
      </div> -->
    </div>
    <div class="select">
      <div class="tit">
        发票类型
      </div>
      <div class="right" @click="showTypeWindow">
            <span class="text">{{typeSelected}}</span>
          <img
            class="right-icon"
            :src="`${$cdnImageUrl}/common/right-arrow-gray.png`"
            alt=""
          />
      </div>
    </div>
    <!-- <div class="tips">
      若该公司首次在海尔智家商城开具增值税专用发票，请仔细核对开票信息确保填写无误，详见
      <span @click="fnChangeNoticeFlag">发票须知</span>。
    </div> -->
    <div class="invoice-normal-wrap" v-if="invoiceData.it === 2 || invoiceData.it === 3">
      <invoice-normal :hideCancelBtn="modifyFlag" :invoiceData="invoiceData" :autoFlag="!!pageInfo.autoInvoiceFlag" :isLocked="pageInfo.hasLocked" :orderNo="orderNo" :hasWa="hasWa" @cancleInvoice="cancelInvoice" @fnRadioChange="fnRadioChange" @fnInput="fnInput" @handleLocation="handleLocation" @fnConfirm="fnConfirm"></invoice-normal>
    </div>
    <div class="invoice-vat-wrap" v-if="invoiceData.it === 1">
      <invoice-vat :hideCancelBtn="modifyFlag" :invoiceData="invoiceData" :isLocked="pageInfo.hasLocked" :orderNo="orderNo" :hasWa="hasWa" @cancleInvoice="cancelInvoice" @fnInput="fnInput" @fnConfirm="fnConfirm" @handleLocation="handleLocation"></invoice-vat>
    </div>
    <div class="invoice-vat-wrap" v-if="invoiceData.it === 4">
      <invoice-vat-electric :hideCancelBtn="modifyFlag" :invoiceData="invoiceData" :isLocked="pageInfo.hasLocked" :orderNo="orderNo" :hasWa="hasWa" @cancleInvoice="cancelInvoice" @fnInput="fnInput" @fnConfirm="fnConfirm" @handleLocation="handleLocation"></invoice-vat-electric>
    </div>
    <!-- <invoice-notice @fnChangeNoticeFlag="fnChangeNoticeFlag" v-if="noticeFlag"></invoice-notice> -->
    <!-- 发票须知 -->
    <van-popup v-model="invoiceTipsShow" position="bottom" :style="{height:'9.46rem'}"  @close="onHideTips" @click="onHideTips">
      <div @click.stop class="content">
      <div class="tips-header">
        <span class="tips-title">发票须知</span>
        <span class="tips-close" @click="onHideTips"></span>
      </div>
      <div class="scroll-view">
        <div class="text">
          1. 第三方卖家销售的商品/服务的发票由卖家自行出具。发票金额不含礼品卡、钻石、优惠券、积分、红包、金币、银行支付有礼等优惠扣减金额。
          <br>
          <br>
          2. 增值税电子普通发票是税务局认可的有效收付款凭证，其法律效力、基本用途及使用规定同纸质发票，如需纸质发票可自行下载打印;
          <br>
          <br>
          3. 开票时间与查询路径：
          <br>
           (1) 电子普通发票在订单完成后3个工作日开具；增值税普通发票(纸质)、增值税专用发票(纸质)在订单完成后7个工作日开具为您寄出；
          <br>
           (2) 可在"我的订单→订单详情→发票服务→发票详情"查询相关信息。
          <br>
          <br>
          4. 发票补开、换开及修改规则：
          <br>
           (1) 下单时未申请开票的订单，签收后365天内可申开票
          <br>
           (2) 订单签收前、订单签收后各可申请一次换开及修改发票；请认真核对开票内容
          <br>
           (3) 更换增值税专用发票，需将原票寄回，系统未自动带出退回地址的请与第三方商家客服确认。
          <br>
          <br>
        </div>
      </div>
    </div>
    </van-popup>
    <invoice-type :invoiceData="pageInfo" :show="showTypeSelect" @onHide="onTypeHide"></invoice-type>
  </div>
</div>
</template>
<script>
import Header from '@/components/header'

// import invoiceNotice from './components/invoice-notice'
import invoiceNormal from './components/invoice-normal'
import invoiceVat from './components/invoice-vat'
import invoiceVatElectric from './components/invoice-vat-electric.vue'
import orderInfo from './components/order-info'
import invoiceType from './components/invoice-type.vue'
import { toInvoice, memberInvoice, defaultInvoiceAddress, submitMemInvoice, submitTailInvoice, getApplyInvoicePage, getReplaceApply, cancelInvoice, submitInvoice, replaceSubmit } from '@/http/service/goods'
export default {
  components: {
    Header,
    // invoiceNotice,
    invoiceNormal,
    invoiceVat,
    invoiceType,
    orderInfo,
    invoiceVatElectric
  },
  data() {
    return {
      noticeFlag: false,
      showTypeSelect: false,
      typeSelected: '增值税电子普通发票',
      orderNo: '', // 网单号
      hasWa: 1, // 是否含有自营商品
      invoiceData: {
        // 普通发票提交信息
        it: 2, // 1：增值税，2：普票
        nit: 1, // 1个人发票，2公司发票
        iti: '', // 发票抬头（增值税发票为公司名称）
        tpn: '', // 公司注册税号
        // 增票提交信息
        bc: '', // 发票标题（增值税发票为公司名称）
        // tpn: '', // 公司注册税号
        rga: '', // 公司注册地
        rgp: '', // 公司注册电话
        bn: '', // 开户行
        cbn: '', // 银行卡号
        rcc: '', // 发票邮寄收件人
        rcr: '', // 发票邮寄地址省市区街道("上海 上海 长宁区,江苏路街道")
        rca: '', // 发票邮寄详细地址
        // rcz: '', // 发票邮寄邮编
        rcm: '', // 发票邮寄电话
        em: '', // 收票邮箱
      },
      pageInfo: {
        hasLocked: 0,
        supportedInvoiceTypes: [2],
      }, // 发票详细信息
      memberInvoiceInfo: [],
      address: '',
      street: '',
      invoiceTipsShow: false, // 发票须知弹窗控制
      modifyFlag: 0
    }
  },
  created() {
    // this.toInvoice()
    this.orderNo = this.$route.query.orderNo
    this.modifyFlag = Number(this.$route.query.modifyFlag ? this.$route.query.modifyFlag : 0)
    // 新发票信息接口
    this.invoiceInfo()
  },
  activated() {
    const info = this.$store.state.orderInvoice.tempInvoice
    if (info) {
      this.invoiceData = {
        ...this.invoiceData,
        bc: info.invoiceTitle,
        tpn: info.taxPayerNumber,
        rga: info.registerAddress,
        rgp: info.registerPhone,
        bn: info.bankName,
        cbn: info.bankCardNumber,
      }
      this.$store.commit('orderInvoice/setTempInvoice', null)
    }
  },
  methods: {
    /**
     * @description 新发票信息接口
     * 若有网单号 调 getInvoiceInfo，无网单号调 getApplyInvoicePage
    */
    invoiceInfo() {
      if (this.orderNo) {
        if (this.modifyFlag) {
          // 修改发票申请时获取发票信息
          this.getReplaceApply()
        } else {
          // 网单过来的 调新发票信息接口
          this.getApplyInvoicePage()
        }
      } else {
        // 下单过来的，调老接口
        this.toInvoice()
      }
    },
    /**
     * @description  发票修改时，请求页面数据
    */
    getReplaceApply() {
      let param = {
        orderProductSn: this.orderNo
      }
      getReplaceApply(param).then(res => {
        if (res.success) {
          this.pageInfo = res.data

          // 如果没有共同发票类型 默认电子发票
          if (!this.pageInfo.supportedInvoiceTypes || this.pageInfo.supportedInvoiceTypes.length === 0) {
            this.pageInfo.supportedInvoiceTypes = [2]
          }
          // 是否含有自营商品
          this.hasWa = res.data.hasWaProduct
          // 根据发票信息 重新绑定已选发票类型
          this.pageInfo.it = this.pageInfo.supportedInvoiceTypes.includes(this.invoiceData.it) ? this.invoiceData.it : this.pageInfo.supportedInvoiceTypes[0]
          this.onTypeHide(this.pageInfo.supportedInvoiceTypes.includes(this.invoiceData.it) ? this.invoiceData.it : this.pageInfo.supportedInvoiceTypes[0])
        } else {
          this.$toast(res.message)
          // 发生错误 提示后返回上一页
          setTimeout(() => {
            this.$store.dispatch('doHistoryBack')
          }, 1500);
        }
      })
    },
    /**
     * @description  单品页/购物车跳转开票 发票信息
    */
    getApplyInvoicePage() {
      let param = {
        orderProductSn: this.orderNo
      }
      getApplyInvoicePage(param).then(res => {
        if (res.success) {
          let memberActInvoice = res.data.memberActInvoice // 专票信息
          this.pageInfo = res.data

          // 如果没有共同发票类型 默认电子发票
          if (!this.pageInfo.supportedInvoiceTypes || this.pageInfo.supportedInvoiceTypes.length === 0) {
            this.pageInfo.supportedInvoiceTypes = [2]
          }
          this.invoiceData = {
            ...this.invoiceData,
            it: res.data.invoiceType, // 1：增值税，2：普电子 3：纸质普票
            nit: res.data.invoiceIdentity, // 1个人发票，2公司发票
            // iti: memberActInvoice && memberActInvoice.invoiceTitle ? memberActInvoice.invoiceTitle : res.data.consignee, // 发票抬头（增值税发票为公司名称）
            // bc: memberActInvoice && memberActInvoice.invoiceTitle ? memberActInvoice.invoiceTitle : res.data.consignee, // 发票标题（增值税发票为公司名称）
            iti: res.data.invoiceType === 1 ? memberActInvoice && memberActInvoice.invoiceTitle ? memberActInvoice.invoiceTitle : '' : res.data.invoiceTitle, // 发票抬头（增值税发票为公司名称）
            bc: res.data.invoiceType === 1 ? memberActInvoice && memberActInvoice.invoiceTitle ? memberActInvoice.invoiceTitle : '' : res.data.invoiceTitle, // 发票标题（增值税发票为公司名称）
            tpn: memberActInvoice && memberActInvoice.taxPayerNumber ? memberActInvoice.taxPayerNumber : '', // 公司注册税号, 取专票信息纳税人识别号
            rga: memberActInvoice && memberActInvoice.registerAddress ? memberActInvoice.registerAddress : '', // 公司注册地
            rgp: memberActInvoice && memberActInvoice.registerPhone ? memberActInvoice.registerPhone : '', // 公司注册电话
            bn: memberActInvoice && memberActInvoice.bankName ? memberActInvoice.bankName : '', // 开户行
            cbn: memberActInvoice && memberActInvoice.bankCardNumber ? memberActInvoice.bankCardNumber : '', // 银行卡号
            rcc: res.data.consignee, // 发票邮寄收件人
            rcr: res.data.region + ',' + res.data.street, // 发票邮寄地址省市区街道("上海 上海 长宁区,江苏路街道")
            rca: res.data.detailedAddress, // 发票邮寄详细地址
            // rcz: res.data.consigneePostcode, // 发票邮寄邮编
            rcm: res.data.consigneeMobile, // 发票邮寄电话
            em: res.data.email || '', // 收票邮箱
          }
          // 默认绑定发票地址
          this.address = res.data.region
          this.street = res.data.street
          // 是否含有自营商品
          this.hasWa = res.data.hasWaProduct
          // 根据发票信息 重新绑定已选发票类型
          this.onTypeHide(this.invoiceData.it)
        } else {
          this.$toast(res.message)
          // 发生错误 提示后返回上一页
          setTimeout(() => {
            this.$store.dispatch('doHistoryBack')
          }, 1500);
        }
      })
    },
    /**
     * @description 发票类型弹窗关闭回调方法
    */
    onTypeHide(value) {
      this.showTypeSelect = false
      switch (value) {
        case 1:
          this.fnChangeType(1)
          this.typeSelected = '增值税专用发票'
          // 专票抬头取值逻辑
          this.invoiceData.iti = (this.pageInfo.memberActInvoice && this.pageInfo.memberActInvoice.invoiceTitle ? this.pageInfo.memberActInvoice.invoiceTitle : '') || this.invoiceData.iti
          this.invoiceData.bc = (this.pageInfo.memberActInvoice && this.pageInfo.memberActInvoice.invoiceTitle ? this.pageInfo.memberActInvoice.invoiceTitle : '') || this.invoiceData.bc
          if (this.orderNo) {
            this.memberInvoice()
          }
          // 专票纳税人识别号取值逻辑 优先展示专票历史
          // this.invoiceData.tpn = this.memberInvoiceInfo.taxPayerNumber || this.pageInfo.memberActInvoice.taxPayerNumber || this.pageInfo.taxPayerNumber || this.pageInfo.tpn
          break;
        case 3:
          this.fnChangeType(3)
          this.typeSelected = '增值税纸质普通发票'

          this.invoiceData.iti = this.pageInfo.invoiceTitle || this.pageInfo.iti
          this.invoiceData.bc = this.pageInfo.invoiceTitle || this.pageInfo.iti
          break;
        case 2:
          this.fnChangeType(2)
          this.typeSelected = '增值税电子普通发票'

          this.invoiceData.iti = this.pageInfo.invoiceTitle || this.pageInfo.iti
          this.invoiceData.bc = this.pageInfo.invoiceTitle || this.pageInfo.iti
          break;
        case 4:
          this.fnChangeType(4)
          this.typeSelected = '增值税电子专用发票'

          this.invoiceData.iti = (this.pageInfo.memberActInvoice && this.pageInfo.memberActInvoice.invoiceTitle ? this.pageInfo.memberActInvoice.invoiceTitle : '') || this.invoiceData.iti
          this.invoiceData.bc = (this.pageInfo.memberActInvoice && this.pageInfo.memberActInvoice.invoiceTitle ? this.pageInfo.memberActInvoice.invoiceTitle : '') || this.invoiceData.bc
          if (this.orderNo) {
            this.memberInvoice()
          }
          break;
      }
    },
    // fnChangeNoticeFlag() {
    //   this.noticeFlag = !this.noticeFlag
    // },
    /**
     * @description 发票须知弹窗关闭
     */
    onHideTips() {
      this.invoiceTipsShow = false
    },
    /**
     * @description 获取结算页带过来的发票信息
     */
    toInvoice() {
      toInvoice(this.$route.query.from === 'tailOrder' ? { orderSn: this.$route.query.orderSn } : {}).then(res => {
        if (res.success) {
          this.pageInfo = res.data

          // 如果没有共同发票类型 默认电子发票
          if (!this.pageInfo.supportedInvoiceTypes || this.pageInfo.supportedInvoiceTypes.length === 0) {
            this.pageInfo.supportedInvoiceTypes = [2]
          }
          this.pageInfo.hasLocked = 0 // 下单开票默认不锁定
          this.invoiceData = {
            ...this.invoiceData,
            it: res.data.it,
            nit: res.data.nit,
            iti: res.data.iti,
            bc: res.data.bc,
            tpn: res.data.tpn,
            rga: res.data.rga,
            rgp: res.data.rgp,
            bn: res.data.bn,
            cbn: res.data.cbn,
            rcc: res.data.rcc,
            rcr: res.data.rcr,
            rca: res.data.rca,
            // rcz: res.data.rcz,
            rcm: res.data.rcm,
            em: res.data.email, // 收票邮箱
          }
          if (res.data.rcr) {
            this.address =
              res.data.rcr.indexOf(',') > -1
                ? res.data.rcr.split(',')[0]
                : res.data.rcr.split(' ')[0] + ' ' + res.data.rcr.split(' ')[1] + ' ' + res.data.rcr.split(' ')[2]
            this.street = res.data.rcr.indexOf(',') > -1 ? res.data.rcr.split(',')[1] : res.data.rcr.split(' ')[3]
          }
          if (!res.data.bc) {
            this.defaultInvoiceAddress()
            this.memberInvoice()
          }
          // 是否含有自营商品
          this.hasWa = res.data.hasWaProduct
          // 根据发票信息 重新绑定已选发票类型
          this.onTypeHide(this.invoiceData.it)
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 获取默认增票信息
     */
    memberInvoice() {
      memberInvoice({
        d: 1,
      }).then(res => {
        if (res.success) {
          if (res.data.length > 0) {
            if (res.data[0]) {
              this.invoiceData = {
                ...this.invoiceData,
                bc: res.data[0].invoiceTitle,
                tpn: this.invoiceData.it === 1 ? res.data[0].taxPayerNumber || this.invoiceData.tpn : this.invoiceData.tpn || res.data[0].taxPayerNumber,
                rga: res.data[0].registerAddress,
                rgp: res.data[0].registerPhone,
                bn: res.data[0].bankName,
                cbn: res.data[0].bankCardNumber,
              }
              this.memberInvoiceInfo = res.data[0] // 记录历史增票信息
              console.log('invoiceData :', this.invoiceData)
            }
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 获取默认增票信息收货地址
     */
    defaultInvoiceAddress() {
      defaultInvoiceAddress(this.$route.query.from === 'tailOrder' ? { orderSn: this.$route.query.orderSn } : {}).then(res => {
        if (res.success) {
          this.invoiceData = {
            ...this.invoiceData,
            rcc: this.invoiceData?.rcc || res.data?.rcc || '',
            rcr: this.invoiceData?.rcr || res.data?.rcr || '',
            rca: this.invoiceData?.rca || res.data?.rca || '',
            // rcz: this.invoiceData.rcz || res.data.rcz,
            rcm: this.invoiceData?.rcm || res.data?.rcm || '',
          }
          if (res.data?.rcr) {
            this.address =
              res.data.rcr.indexOf(',') > -1
                ? res.data.rcr.split(',')[0]
                : res.data.rcr.split(' ')[0] + ' ' + res.data.rcr.split(' ')[1] + ' ' + res.data.rcr.split(' ')[2]
            this.street = res.data.rcr.indexOf(',') > -1 ? res.data.rcr.split(',')[1] : res.data.rcr.split(' ')[3]
          }
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 切换发票类型
     */
    fnChangeType(type) {
      this.invoiceData.it = type
    },
    fnRadioChange(nit) {
      this.invoiceData.nit = nit
    },
    fnInput(data) {
      switch (data.type) {
        case 'iti':
          this.invoiceData.iti = data.value
          break
        case 'tpn':
          this.invoiceData.tpn = data.value
          break
        case 'bc':
          this.invoiceData.bc = data.value
          break
        case 'rga':
          this.invoiceData.rga = data.value
          break
        case 'rgp':
          this.invoiceData.rgp = data.value
          break
        case 'bn':
          this.invoiceData.bn = data.value
          break
        case 'cbn':
          this.invoiceData.cbn = data.value
          break
        case 'rcc':
          this.invoiceData.rcc = data.value
          break
        case 'rca':
          this.invoiceData.rca = data.value
          break
        // case 'rcz':
        //   this.invoiceData.rcz = data.value
        //   break
        case 'rcm':
          this.invoiceData.rcm = data.value
          break
        case 'em':
          this.invoiceData.em = data.value
          break
      }
    },
    handleLocation(location) {
      console.log(location)
      this.address = location.address
      this.street = location.street
      this.invoiceData = {
        ...this.invoiceData,
        rcr: this.address + ',' + this.street,
      }
    },
    fnConfirm() {
      if (this.orderNo) {
        if (this.modifyFlag) {
          // 修改发票申请
          this.replaceSubmit()
        } else {
          // 网单开票流程
          this.submitInvoice()
        }
      } else {
        // 结算页下单开票流程
        let _params = {}
        if (this.invoiceData.it === 2 || this.invoiceData.it === 3) {
          _params = {
            it: this.invoiceData.it,
            nit: this.invoiceData.nit,
            iti: this.invoiceData.iti,
          }
          // 自动开票且 电子发票增加邮箱入参
          if (this.invoiceData.it === 2 && !!this.pageInfo.autoInvoiceFlag) {
            _params = {
              ..._params,
              email: this.invoiceData.em,
            }
          }
          if (this.invoiceData.nit === 2) {
            _params = {
              ..._params,
              tpn: this.invoiceData.tpn,
            }
          }
          // 纸质普票 增加收货信息
          if (this.invoiceData.it === 3) {
            _params = {
              ..._params,
              rcc: this.invoiceData.rcc,
              rca: this.invoiceData.rca,
              rcm: this.invoiceData.rcm,
              rcr: this.address + ',' + this.street,
            }
          }
        } else {
          _params = {
            ...this.invoiceData,
            iti: this.invoiceData.bc,
            rcr: this.address + ',' + this.street
          }
          if (this.invoiceData.it === 4) {
            _params.rcr = ''
            _params.rcc = ''
            _params.rca = ''
            _params.rcm = ''
          }
        }
        let _flag = this.checkFn(_params)
        if (_flag) {
          this.submitMemInvoice(_params)
        }
      }
    },
    checkFn(params) {
      let title = ''
      if (params.it === 1) {
        // 增票
        if (!/^.[^&<>]{0,}$/.test(params.bc) || !params.bc) {
          title = '公司名称必填,且不能包含特殊字符'
        } else if (params.bc.length < 5) {
          title = '公司名称有误，请重新填写'
        } else if (!/^[0-9A-Za-z]{18}$|^[0-9A-Za-z]{15}$|^[0-9A-Za-z]{20}$/.test(params.tpn)) {
          title = '纳税人识别号为15位或18位或20位的数字或字母'
        } else if (!/^.[^&<>]{4,}$/.test(params.rga) || !params.rga) {
          title = '注册地址至少5个字符，且不能包含特殊字符'
        } else if (!params.rgp || !/^((0\d{2,3}-\d{7,8})|(1[3457689]\d{9}))$/.test(params.rgp)) {
          title = '注册电话格式不正确'
        } else if (!params.bn) {
          title = '开户银行为必填'
        } else if (!params.cbn) {
          title = '开户账号为必填'
        } else if (!params.rcc) {
          title = '收件人姓名为必填'
        } else if (!params.rca) {
          title = '发票邮寄详细地址为必填'
        } else if (!params.rcm) {
          title = '发票邮寄收件人手机号为必填'
        } else if (!/^1([3456789][0-9]|4[01356789]|66|9[89])\d{8}$/.test(params.rcm)) {
          title = '发票邮寄收件人手机号格式不对'
        }
      } else if (params.it === 4) {
        // 电子专票
        if (!/^.[^&<>]{0,}$/.test(params.bc) || !params.bc) {
          title = '公司名称必填,且不能包含特殊字符'
        } else if (params.bc.length < 5) {
          title = '公司名称有误，请重新填写'
        } else if (!/^[0-9A-Za-z]{18}$|^[0-9A-Za-z]{15}$|^[0-9A-Za-z]{20}$/.test(params.tpn)) {
          title = '纳税人识别号为15位或18位或20位的数字或字母'
        } else if (!/^.[^&<>]{4,}$/.test(params.rga) || !params.rga) {
          title = '注册地址至少5个字符，且不能包含特殊字符'
        } else if (!params.rgp || !/^((0\d{2,3}-\d{7,8})|(1[3457689]\d{9}))$/.test(params.rgp)) {
          title = '注册电话格式不正确'
        } else if (!params.bn) {
          title = '开户银行为必填'
        } else if (!params.cbn) {
          title = '开户账号为必填'
        }
      } else {
        if ((params.it === 2 && params.nit === 1) || (params.it === 3 && params.nit === 1)) {
          // 个人
          if (!/^.[^&<>]{0,}$/.test(params.iti) || !params.iti) {
            title = '发票抬头必填,且不能包含特殊字符'
          }
        } else {
          // 公司
          if (!/^.[^&<>]{0,}$/.test(params.iti) || !params.iti) {
            title = '发票抬头必填,且不能包含特殊字符'
          } else if (!/^[0-9A-Za-z]{18}$|^[0-9A-Za-z]{15}$|^[0-9A-Za-z]{20}$/.test(params.tpn)) {
            title = '纳税人识别号为15位或18位或20位的数字或字母'
          }
        }
        // 自动开票的电票需要校验邮箱
        if (params.it === 2 && !!this.pageInfo.autoInvoiceFlag) {
          if (!this.$util.isEmail(params.email)) {
            title = '收票邮箱格式不正确'
          }
        }
      }
      if (title) {
        this.$toast(title)
        return false
      } else {
        return true
      }
    },
    /**
     * @description 网单开票检查参数
    */
    checkOrderFn(params) {
      let title = ''
      if (params.invoiceType === 1) {
        // 增票
        if (!/^.[^&<>]{0,}$/.test(params.invoiceTitle) || !params.invoiceTitle) {
          title = '公司名称必填,且不能包含特殊字符'
        } else if (params.invoiceTitle.length < 5) {
          title = '公司名称有误，请重新填写'
        } else if (!/^[0-9A-Za-z]{18}$|^[0-9A-Za-z]{15}$|^[0-9A-Za-z]{20}$/.test(params.taxpayerNumber)) {
          title = '纳税人识别号为15位或18位或20位的数字或字母'
        } else if (!/^.[^&<>]{4,}$/.test(params.regiterAddress) || !params.regiterAddress) {
          title = '注册地址至少5个字符，且不能包含特殊字符'
        } else if (!params.registerTel || !/^((0\d{2,3}-\d{7,8})|(1[3457689]\d{9}))$/.test(params.registerTel)) {
          title = '注册电话格式不正确'
        } else if (!params.bankName) {
          title = '开户银行为必填'
        } else if (!params.bankAccount) {
          title = '开户账号为必填'
        } else if (!params.consignee) {
          title = '收件人姓名为必填'
        } else if (!params.detailedAddress) {
          title = '发票邮寄详细地址为必填'
        } else if (!params.consigneeMobile) {
          title = '发票邮寄收件人手机号为必填'
        } else if (!/^1([3456789][0-9]|4[01356789]|66|9[89])\d{8}$/.test(params.consigneeMobile)) {
          title = '发票邮寄收件人手机号格式不对'
        }
      } else if (params.invoiceType === 4) {
        // 电子专票
        if (!/^.[^&<>]{0,}$/.test(params.invoiceTitle) || !params.invoiceTitle) {
          title = '公司名称必填,且不能包含特殊字符'
        } else if (params.invoiceTitle.length < 5) {
          title = '公司名称有误，请重新填写'
        } else if (!/^[0-9A-Za-z]{18}$|^[0-9A-Za-z]{15}$|^[0-9A-Za-z]{20}$/.test(params.taxpayerNumber)) {
          title = '纳税人识别号为15位或18位或20位的数字或字母'
        } else if (!/^.[^&<>]{4,}$/.test(params.regiterAddress) || !params.regiterAddress) {
          title = '注册地址至少5个字符，且不能包含特殊字符'
        } else if (!params.registerTel || !/^((0\d{2,3}-\d{7,8})|(1[3457689]\d{9}))$/.test(params.registerTel)) {
          title = '注册电话格式不正确'
        } else if (!params.bankName) {
          title = '开户银行为必填'
        } else if (!params.bankAccount) {
          title = '开户账号为必填'
        }
      } else {
        if ((params.invoiceType === 2 && params.invoiceIdentity === 1) || (params.invoiceType === 3 && params.invoiceIdentity === 1)) {
          // 个人
          if (!/^.[^&<>]{0,}$/.test(params.invoiceTitle) || !params.invoiceTitle) {
            title = '发票抬头必填,且不能包含特殊字符'
          }
        } else {
          // 公司
          if (!/^.[^&<>]{0,}$/.test(params.invoiceTitle) || !params.invoiceTitle) {
            title = '发票抬头必填,且不能包含特殊字符'
          } else if (!/^[0-9A-Za-z]{18}$|^[0-9A-Za-z]{15}$|^[0-9A-Za-z]{20}$/.test(params.taxpayerNumber)) {
            title = '纳税人识别号为15位或18位或20位的数字或字母'
          }
        }
        // 自动开票的电票需要校验邮箱
        if (params.invoiceType === 2 && !!this.pageInfo.autoInvoiceFlag) {
          if (!this.$util.isEmail(params.email)) {
            // /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/
            title = '收票邮箱格式不正确'
          }
        }
      }
      if (title) {
        this.$toast(title)
        return false
      } else {
        return true
      }
    },
    /**
     * @description 提交发票信息
     */
    submitMemInvoice(params) {
      // submitTailInvoice
      if (this.$route.query.from === 'tailOrder') {
        submitTailInvoice({ ...params, orderSn: this.$route.query.orderSn }).then(res => {
          if (res.success) {
            this.$router.go(-1)
          } else {
            this.$toast(res.message)
          }
        })
      } else {
        submitMemInvoice(params).then(res => {
          if (res.success) {
            this.$router.go(-1)
          } else {
            this.$toast(res.message)
          }
        })
      }
    },
    /**
     * @description 提交发票信息 (网单开票调用)
     */
    submitInvoice() {
      let param = {
        orderProductSn: this.orderNo,
        invoiceType: this.invoiceData.it,
        invoiceIdentity: this.invoiceData.nit,
        invoiceTitle: this.invoiceData.it === 1 ? this.invoiceData.bc : this.invoiceData.iti,
        taxpayerNumber: this.invoiceData.tpn,
        regiterAddress: this.invoiceData.rga,
        registerTel: this.invoiceData.rgp,
        bankName: this.invoiceData.bn,
        bankAccount: this.invoiceData.cbn,
        region: this.invoiceData.rcr ? this.invoiceData.rcr.split(',')[0] : '',
        street: this.invoiceData.rcr ? this.invoiceData.rcr.split(',')[1] : '',

        detailedAddress: this.invoiceData.rca,
        consignee: this.invoiceData.rcc,
        consigneeMobile: this.invoiceData.rcm,
        // consigneePostcode: this.invoiceData.rcz,
      }
      let _param = {
        orderProductSn: this.orderNo,
        autoInvoiceFlag: this.pageInfo.autoInvoiceFlag
      }

      if (this.invoiceData.it === 2 || this.invoiceData.it === 3) {
        _param = {
          ..._param,
          invoiceType: this.invoiceData.it,
          invoiceIdentity: this.invoiceData.nit,
          invoiceTitle: this.invoiceData.it === 1 ? this.invoiceData.bc : this.invoiceData.iti,
        }
        // 自动开票且 电子发票增加邮箱入参
        if (this.invoiceData.it === 2 && !!this.pageInfo.autoInvoiceFlag) {
          _param = {
            ..._param,
            email: this.invoiceData.em,
          }
        }
        if (this.invoiceData.nit === 2) {
          _param = {
            ..._param,
            taxpayerNumber: this.invoiceData.tpn,
          }
        }
        // 纸质普票 增加收货信息
        if (this.invoiceData.it === 3) {
          _param = {
            ..._param,
            consignee: this.invoiceData.rcc,
            detailedAddress: this.invoiceData.rca,
            region: this.address,
            street: this.street,
            consigneeMobile: this.invoiceData.rcm,
          }
        }
      } else {
        _param = {
          ...param,
          invoiceTitle: this.invoiceData.bc,
          // detailedAddress: this.invoiceData.rca,
          region: this.address,
          street: this.street
        }
      }
      let _flag = this.checkOrderFn(_param)
      if (_flag) {
        submitInvoice(_param).then(res => {
          if (res.success) {
            this.$toast.success('您的发票申请已提交成功！')
            this.$router.go(-1)
          } else {
            this.$toast(res.message)
          }
        })
      }
    },
    /**
     * @description 提交发票信息 (修改发票申请时调用)
     */
    replaceSubmit() {
      let param = {
        orderProductSn: this.orderNo,
        invoiceType: this.invoiceData.it,
        invoiceIdentity: this.invoiceData.nit,
        invoiceTitle: this.invoiceData.it === 1 ? this.invoiceData.bc : this.invoiceData.iti,
        taxpayerNumber: this.invoiceData.tpn,
        regiterAddress: this.invoiceData.rga,
        registerTel: this.invoiceData.rgp,
        bankName: this.invoiceData.bn,
        bankAccount: this.invoiceData.cbn,
        region: this.invoiceData.rcr ? this.invoiceData.rcr.split(',')[0] : '',
        street: this.invoiceData.rcr ? this.invoiceData.rcr.split(',')[1] : '',

        detailedAddress: this.invoiceData.rca,
        consignee: this.invoiceData.rcc,
        consigneeMobile: this.invoiceData.rcm,
        // consigneePostcode: this.invoiceData.rcz,
      }
      let _param = {
        orderProductSn: this.orderNo,
        autoInvoiceFlag: this.pageInfo.autoInvoiceFlag
      }

      if (this.invoiceData.it === 2 || this.invoiceData.it === 3) {
        _param = {
          ..._param,
          invoiceType: this.invoiceData.it,
          invoiceIdentity: this.invoiceData.nit,
          invoiceTitle: this.invoiceData.it === 1 ? this.invoiceData.bc : this.invoiceData.iti,
        }
        // 自动开票且 电子发票增加邮箱入参
        if (this.invoiceData.it === 2 && !!this.pageInfo.autoInvoiceFlag) {
          _param = {
            ..._param,
            email: this.invoiceData.em,
          }
        }
        if (this.invoiceData.nit === 2) {
          _param = {
            ..._param,
            taxpayerNumber: this.invoiceData.tpn,
          }
        }
        // 纸质普票 增加收货信息
        if (this.invoiceData.it === 3) {
          _param = {
            ..._param,
            consignee: this.invoiceData.rcc,
            detailedAddress: this.invoiceData.rca,
            region: this.address,
            street: this.street,
            consigneeMobile: this.invoiceData.rcm,
          }
        }
      } else {
        _param = {
          ...param,
          invoiceTitle: this.invoiceData.bc,
          // detailedAddress: this.invoiceData.rca,
          region: this.invoiceData.it === 1 ? this.address : '',
          street: this.invoiceData.it === 1 ? this.street : ''
        }
      }
      let _flag = this.checkOrderFn(_param)
      if (_flag) {
        const invoiceType = Number(this.$route.query.invoiceType)
        if ([1, 2, 3, 4].includes(invoiceType)) {
          this.$dialog.confirm({
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            messageAlign: 'center',
            cancelButtonColor: '#999999',
            message: '您好，请您核对好需要修改的发票信息，此功能仅可修改一次'
          }).then(() => {
            replaceSubmit(_param).then(res => {
              if (res.success) {
                if ([1, 3].includes(invoiceType)) {
                  this.$dialog.confirm({
                    messageAlign: 'left',
                    cancelButtonColor: '#999999',
                    confirmButtonText: '立即填写',
                    cancelButtonText: '以后再说',
                    title: '您的发票修改申请已提交成功！',
                    message: `鉴于原发票类型为${invoiceType === 1 ? '增值税专用发票' : '增值税纸质普通发票'}，请于5日内将原票寄回并上传退回快递单号,逾期本次申请自动作废。 是否立即填写发票邮寄快递单号？`
                  }).then(() => {
                    this.$router.replace({
                      path: '/invoiceExpress',
                      query: {
                        orderNo: this.orderNo,
                        invoiceType: this.invoiceData.it
                      }
                    })
                  }).catch(() => {
                    this.$router.go(-1)
                  })
                } else {
                  this.$toast.success('您的发票修改申请已提交成功！')
                  this.$router.go(-1)
                }
              } else {
                if (res.errorCode === 'INV0003') {
                  this.$dialog.alert({
                    message: '本次修改失败，修改发票仅可一次',
                    messageAlign: 'center',
                  })
                } else {
                  this.$toast.fail(res.message)
                }
              }
            })
          }).catch(() => {})
        } else {
          replaceSubmit(_param).then(res => {
            if (res.success) {
              this.$toast.success('您的发票修改申请已提交成功！')
              this.$router.go(-1)
            } else {
              this.$toast.fail(res.message)
            }
          })
        }
      }
    },
    /**
     * @description 不开发票处理逻辑
     */
    cancelInvoice() {
      if (this.orderNo) {
        // 网单开票直接返回
        history.go(-1)
      } else {
        // 下单时取消发票,先清空之前录入信息
        cancelInvoice(this.$route.query.from === 'tailOrder' ? { orderSn: this.$route.query.orderSn } : {}).then(res => {
          if (res.success) {
          // 取消成功，返回上一页
            history.go(-1)
          }
        })
      }
    },
    /**
     * @description 打开发票类型选择
    */
    showTypeWindow() {
      // 多网单已开票状态下锁定不允许打开
      if (this.pageInfo.hasLocked === 1) {
        return
      }
      this.showTypeSelect = true
    }
  },
  beforeDestroy() {
    this.$store.commit('orderInvoice/setTempInvoice', null)
  }
}
</script>
