            :disabled="isLocked===1"
            @input="fnInput('rgp')"
            autosize
            type="textarea"
            placeholder="座机（区号-号码）或手机"
          />
        </div>
      </li>
      <li>
        <div class="left"><!--<span>*</span>-->开户银行</div>
        <div class="right">
          <!-- <input placeholder="请填写开户银行" type="text" v-model="bn" :disabled="isLocked===1" @input="fnInput('bn')" /> -->
          <van-field
