          />
        </div>
      </li>
    </ul>
    <!-- <div class="tips">发票邮寄地址：如发票与商品发往不同地址，请修改邮寄地址信息。</div> -->
    <div class="edit-location-tip">
      <span v-if="isLocked===1">该订单存在已经申请的开票信息，发票信息不需更改，如需帮助请联系客服</span>
    </div>
    <div class="btn-wrap">
      <div class="btn" @click="fnConfirm">确定</div>
      <div class="no-invoice" @click="cancleInvoice" v-if="hasWa === 0 && !orderNo">不开发票</div>
      <div class="no-invoice" @click="doBack" v-if="hasWa === 0 && orderNo">不开发票</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['invoiceData', 'isLocked', 'hasWa', 'orderNo'],
  data() {
    return {
      bc: '',
      tpn: '',
      rga: '',
      rgp: '',
      bn: '',
      cbn: '',
      pi: '',
      ci: '',
      ri: '',
      si: '',
    }
  },
  watch: {
    invoiceData: {
      handler() {
        this.bc = this.invoiceData.bc || ''
        this.tpn = this.invoiceData.tpn || ''
        this.rga = this.invoiceData.rga || ''
        this.rgp = this.invoiceData.rgp || ''
        this.bn = this.invoiceData.bn || ''
        this.cbn = this.invoiceData.cbn || ''
      },
      immediate: true,
    },
  },
  methods: {
    fnInput(type) {
      let _value
      switch (type) {
        case 'bc':
          _value = this.bc
          break
        case 'tpn':
          _value = this.tpn
          break
        case 'rga':
          _value = this.rga
          break
        case 'rgp':
          _value = this.rgp
          break
        case 'bn':
          _value = this.bn
          break
        case 'cbn':
          _value = this.cbn
          break
      }
      this.$emit('fnInput', {
        type: type,
        value: _value ? (_value.trim ? _value.trim() : _value) : '',
      })
    },
    /**
     * @description 不开发票
     */
    cancleInvoice() {
      this.$emit('cancleInvoice')
    },
    /**
     * @description 点击选择所在地区 取消关闭回调
     * @param
     */

    fnConfirm() {
      this.$emit('fnConfirm')
    },
    doBack() {
      this.$store.dispatch('doHistoryBack')
    }
  },
}
</script>

